@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;400;600;800&family=Ubuntu:wght@300;400;500;700&display=swap");

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
html {
    font-size: 62.5%;
}
body {
    font-family: "Ubuntu",sans-serif;
    background-color: #111;
}

a {
    text-decoration: none;
    color: white;
}

button {
    border: none;
    outline: none;
}

input {
    outline: none;
}